body {
  margin: 0;
}

html {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

:root {
  --for-link: #00acdc;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo_img {
  width: 40px;
  height: 40px;
  margin-top: 10px;
}

.nav {
  display: flex;
}

.nav_ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_li {
  margin-right: 20px;
}

.nav_li:last-child {
  margin-right: 0;
}

.nav_a {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--for-link);
  position: relative;
}

.nav_a:hover {
  -webkit-text-decoration: underline var(--for-link);
  text-decoration: underline var(--for-link);
}

.blue:hover {
  color: var(--for-link);
  text-decoration: none !important;
}

.a:first-line {
  font-size: 12px;
}

.is-active:after {
  content: "";
  background-color: var(--for-link);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
